import React from "react";
import { Container } from "react-bootstrap";
import { Calendar, momentLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import moment from "moment";
import "moment/locale/pt-br";
import './CSS/Estilo.css';

const localizer = momentLocalizer(moment);

const messages = {
  today: "Hoje",
  previous: "Anterior",
  next: "Prximo",
};

const CalendarioEvento = ({ eventos }) => {
  const eventList = eventos.map((evento) => ({
    title: evento.nomeEvento,
    start: moment(`${evento.DataEvento} ${evento.HoraEvento}`, "YYYY-MM-DD HH:mm").toDate(),
    end: moment(`${evento.DataEvento} ${evento.HoraEvento}`, "YYYY-MM-DD HH:mm").toDate(), // Define a data de trmino do evento como a mesma data/hora do incio
  }));

  // Componente personalizado para exibir o ttulo e a hora do evento
  const EventComponent = ({ event }) => (
    <div className="event-container">
      <strong>{event.title}</strong>
      <br />
      <span className="event-time d-flex justify-content-start">
        Hora: {moment(event.start).format("HH:mm")}
      </span>
    </div>
  );


  return (
    <Container>
      <div className="calendario-container" style={{ marginBottom: "20%" }}>
        <Calendar
          localizer={localizer}
          events={eventList}
          views={{ month: true }} // Define as visualizaes disponveis, incluindo a do ms
          defaultView="month" // Define a visualizao inicial do calendrio como "month" (modo de visualizao do ms)
          toolbar={true} // Remove a barra de ferramentas do calendrio
          messages={messages} // Define as mensagens para os botes de navegao
          components={{
            event: EventComponent, // Importe e utilize o componente personalizado para exibir o ttulo e a hora do evento
          }}

          dayHeaderFormat={(date, culture, localizer) =>
            localizer.format(date, "ddd", culture).toUpperCase()
          }
          monthHeaderFormat={(date, culture, localizer) =>
            localizer.format(date, "MMMM YYYY", culture).toUpperCase()
          }
          style={{ fontFamily: "Arial, sans-serif" }}
          showMultiDayTimes // Adiciona o boto "ver mais" quando h vrios eventos no mesmo dia
          max={5} // Define o nmero mximo de eventos que sero exibidos em uma clula de dia


        />
      </div>
    </Container>
  );
};

export default CalendarioEvento;

